import React from "react";
import { Select } from "antd";

export default function SimpleTimeSelectBox(props) {
  return (
    <Select {...props}>
      <Select.Option value="* * * * *">Every Minute</Select.Option>
      <Select.Option value="*/5 * * * *">Every 5 Minutes</Select.Option>
      <Select.Option value="*/15 * * * *">Every 15 Minutes</Select.Option>
      <Select.Option value="*/30 * * * *">Every 30 Minutes</Select.Option>
      <Select.Option value="0 * * * *">Every Hour</Select.Option>
      <Select.Option value="0 */4 * * *">Every 4 Hours</Select.Option>
      <Select.Option value="0 */8 * * *">Every 8 Hours</Select.Option>
      <Select.Option value="0 0 * * *">Every Day at Midnight</Select.Option>
      <Select.Option value="0 0 * * SAT">Saturdays at Midnight</Select.Option>
      <Select.Option value="*/60 8-17 * * *">
        Every Hour between 8am and 5pm
      </Select.Option>
      <Select.Option value="*/60 0-8,17-23 * * *">
        Every Hour Outside of 8am and 5pm
      </Select.Option>
    </Select>
  );
}
